






































import { Component, Vue } from 'vue-property-decorator';
import column from '../components/column.vue';
import Store from '../modules/store';
 
@Component({
    components: {
        column
    }
})
export default class HomePage extends Vue {

    private author = Store.authorInfo;

    private pageData = Store.homeData;

    /** 视图列表 */
    get viewList() {
        let list = this.pageData.columnList;
        if (this.pageData.sideIndex > 0) {
            list = list.filter(item => item.type == this.pageData.sideIndex);
        }
        switch (this.pageData.timeIndex) {
            case 0:
                list.sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime());
                break;
            
            case 1:
                list.sort((a, b) => new Date(a.date).getTime() - new Date(b.date).getTime());
                break;

            case 2:
                list.sort((a, b) => Math.random() > 0.5 ? -1 : 1);
                break;
        }
        return list;
    }

    /**
     * 跳转详情
     * @param id 栏目id
     */
    private openDetail(id: number) {
        // console.log('栏目id >>', id);
        this['$router'].push(`/detail/${id}`);
    }
}

